@font-face {
  font-family: 'Roboto';
  src: url('img/Roboto-Black.ttf') format('truetype');
  font-weight: 900;
  font-style: normal;
 }

@font-face {
  font-family: 'Roboto';
  src: url('img/Roboto-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Roboto';
  src: url('img/Roboto-Regular.ttf') format('truetype');
  font-weight: regular;
  font-style: normal;
}

.main {
  position: absolute;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: start;
      align-items: flex-start;
}

.header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}


.btn {
  font-size: large;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-left: 3%;
  padding-right: 3%;
  cursor: pointer;
}

.btn.clear {
  height: 100%;
}

.btn.run {
  height: 100%;
}

.btn.close {
  height: 100%;
}

.img {
  height: 50%;
  width: auto;
}

.fullscreen {
    height: 100%;
    width: 100%;
    position: fixed;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;

    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    color: #FFFFFF;

    text-transform: uppercase;
}

.input {

  width: 94%;
  -ms-flex: 1 1;
      flex: 1 1;
  margin-left: 3%;

  font-family: Roboto;
  font-style: normal;
  font-weight: 900;
  color: #ffffff;
  font-size: 3em;
  line-height: 1;

  border: none;
  overflow: auto;
  outline: none;

  -webkit-box-shadow: none;
  box-shadow: none;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: white;
  opacity: 0.75;
}

.logo{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}


.store {
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  width: auto;
  padding-left: 3%;
  padding-top: 15px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.store_badge {
  height: 100%;
  width: auto;
  padding-right: 15px;
  cursor: pointer;
}

@media all and (orientation:portrait) {
  .header {
    height: 10%;
  }
}

@media all and (orientation:landscape) {
  .header {
    height: 20%;
  }
}

.credits{
  font-size: 1em;
  font-family: Roboto;
  font-style: normal;
  font-weight: regular;
  line-height: 1;
  color: #ffffff;
  opacity: 0.8;
  margin-left: 3%;
  margin-top: 15px;
  margin-bottom: 15px;
}

.a {
  color: #ffffff;
}

@media all and (max-width: 360px) {
  .store {
    height: 40px;
    padding-top: 10px;
  }

  .store_badge {
    padding-right: 5px;
  }

  .credits {
    font-size: 0.8em;
    margin-top: 10px;
    margin-bottom: 10px;
  }
}


